body {
  font-family: 'Poppins', sans-serif;
  background: linear-gradient(135deg, #a1ffce 0%, #faffd1 100%);
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

ul,
li {
  list-style: none;
}

h1 {
  text-align: center;
  color: #ff4500;
  font-size: 3rem;
  margin-bottom: 20px;
}

#createPostForm {
  background: linear-gradient(135deg, #00c3ff 0%, #ffff1c 100%);
  padding: 25px 30px;
  border-radius: 16px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
  width: 100%;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  gap: 18px;
  margin-bottom: 40px;
}

#nameInput,
#titleInput,
#contentInput {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 2px solid #20b2aa;
  border-radius: 5px;
  font-size: 1rem;
  box-sizing: border-box;
}

#createPostBtn {
  padding: 14px 18px;
  font-size: 1.1rem;
  background: linear-gradient(135deg, #38ef7d 0%, #11998e 100%);
  color: white;
  font-weight: bold;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.3s, background 0.3s;
}

#createPostBtn:hover {
  transform: scale(1.05);
  background: linear-gradient(135deg, #11998e 0%, #38ef7d 100%);
}

#postsContainer {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
}

.post {
  background-color: #fff8dc; /* світлий кремовий */
  border-left: 5px solid #ffa500; /* яскравий помаранчевий */
  border-radius: 10px;
  padding: 15px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.user-name,
.user-title {
  margin: 0 0 5px 0;
  color: #ff4500;
  font-size: 1.2rem;
}

.user-content {
  font-size: 1rem;
  margin-bottom: 10px;
  color: #333;
}

.edit-post-btn,
.delete-post-btn {
  margin-right: 10px;
  padding: 5px 10px;
  font-size: 0.9rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.edit-post-btn {
  background-color: #1e90ff;
  color: white;
}

.delete-post-btn {
  background-color: #dc143c;
  color: white;
}

.comments-box {
  margin-top: 15px;
  background-color: #f0f8ff;
  padding: 10px;
  border-radius: 8px;
}

.comments-box h3 {
  margin-top: 0;
  font-size: 1.1rem;
  color: #20b2aa;
}

.comments-box ul {
  list-style-type: disc;
  padding-left: 20px;
  max-height: 120px;
  overflow-y: auto;
  margin-bottom: 10px;
}

.comments-box li {
  font-size: 0.9rem;
  margin-bottom: 5px;
}

.create-comment-form {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.comment-input {
  flex-grow: 1;
  padding: 5px;
  border: 1px solid #20b2aa;
  border-radius: 5px;
  font-size: 0.9rem;
  min-width: 0;
}

.add-comment-btn {
  padding: 5px 10px;
  background-color: #32cd32;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 0.9rem;
  cursor: pointer;
}

